<template>
  <div class="update_bhd">
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title="选择年月日"
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="$emit(`close`)"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUser } from '@/api/profile.js';
import dayjs from 'dayjs';

export default {
  name: 'UpdateBhd',
  props: {
    value: {
      type: [Number, String],
    },
  },
  data() {
    return {
      minDate: new Date(1920, 1, 1),
      maxDate: new Date(Date.now()),
      currentDate: new Date(this.value),
    };
  },
  methods: {
    // 确定提交修改用户昵称
    async onConfirm(date) {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
      });
      const newTime = dayjs(date).format('YYYY-MM-DD');
      try {
        await updateUser({
          birthday: newTime,
        });
        this.$toast.success('保存成功');
        this.$emit('input', newTime);
        this.$emit('close');
      } catch (e) {
        console.log(e);
      }
    },
  },
  created() {},
};
</script>

<style lang="less" scoped>
/deep/.van-picker__toolbar {
  background-color: #1989fa;
  color: #fff;
  button {
    color: #fff;
  }
  .van-picker__title {
    font-size: 15px;
  }
}
</style>
